.banner {
  color: white;
  height: 95vh;
  min-width: 100%;
  position: relative;
}

.banner__contents {
  padding: 2rem;
  position: absolute;
  top: 70%;
  left: 0;
  margin-left: 85px;
  white-space: nowrap;
  /* border: 1px solid red; */
}

