}
.banner__btn {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  font-size: 12px;
  display: inline-block;
  padding: 10px 30px;
  font-weight: 600;
  line-height: 2;
  letter-spacing: 2px;
  text-decoration: none;
  text-transform: uppercase;
  background-color: transparent;
